import { FileSsisServicesProvider } from "../../providers/file-ssis-services/file-ssis-services";
import { GlobalComponent } from "../../components/global/global";
import { ModalController, LoadingController } from "ionic-angular";
import { Component } from "@angular/core";
import { String } from "../../Model/String";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { FileSsisViewModel } from "../../Model/ViewModel/FileSsisViewModel";
import { FilePathServicesProvider } from "../../providers/file-path-services/file-path-services";
import { FilePathViewModel } from "../../Model/ViewModel/FilePathViewModel";
import { ConnServicesProvider } from "../../providers/conn-services/conn-services";
import { RemoteViewModel } from "../../Model/ViewModel/RemoteViewModel";

/**
 * Generated class for the FileSsisModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-file-ssis-add-edit-modal",
  templateUrl: "file-ssis-add-edit-modal.html"
})
export class FileSsisAddEditModalPage {
  func_no: number = 12;
  func_key: string = "014|" + this.func_no;
  item: FileSsisViewModel;
  default_path: FilePathViewModel[];
  ssis_package_path: FilePathViewModel;
  ssis_project_path: FilePathViewModel;
  remote_bk_file_path: FilePathViewModel;
  mode: string;
  CanEditBatch: boolean;
  CanFileBrowser: boolean;
  title: string;
  FileSsis_type: string;
  min_exec_file_seq: number = 120000;
  max_exec_file_seq: number = this.min_exec_file_seq + 9999;
  range_exec_file_seq: string;
  change_mode: string;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    private FileSsisServices: FileSsisServicesProvider,
    private FilePathServices: FilePathServicesProvider,
    public loadingCtrl: LoadingController,
    private global: GlobalComponent,
    public modalCtrl: ModalController,
    private ConnServices: ConnServicesProvider
  ) {
    this.range_exec_file_seq = String.Format(
      "{0}-{1}",
      this.min_exec_file_seq,
      this.max_exec_file_seq
    );
    this.mode = navParams.data.mode;
    this.change_mode = navParams.data.change_mode;
    this.item = new FileSsisViewModel();
    this.item.exec_file_seq = navParams.data.item.exec_file_seq;
    this.item.exec_group = navParams.data.item.exec_group;
    this.item.file_keyword = navParams.data.item.file_keyword;
    this.item.file_min_length = navParams.data.item.file_min_length;
    this.item.file_max_length = navParams.data.item.file_max_length;
    this.item.is_active = navParams.data.item.is_active;
    this.item.exec_seq = navParams.data.item.exec_seq;
    this.item.can_rerun = navParams.data.item.can_rerun;
    this.item.parameter_column_group =
      navParams.data.item.parameter_column_group;
    this.item.creator = navParams.data.item.creator;
    this.item.create_time = navParams.data.item.create_time;
    this.item.modifier = navParams.data.item.modifier;
    this.item.last_update_time = navParams.data.item.last_update_time;
    this.item.note = navParams.data.item.note;
    this.item.package_src_key = navParams.data.item.package_src_key;
    this.item.package_src = navParams.data.item.package_src;
    this.item.is_fail_stop = navParams.data.item.is_fail_stop;
    this.item.ssis_package_path_key = navParams.data.item.ssis_package_path_key;
    this.item.ssis_package_path = navParams.data.item.ssis_package_path;

    this.item.ssis_project_name_key = navParams.data.item.ssis_project_name_key;
    this.item.ssis_project_name = navParams.data.item.ssis_project_name;


    this.item.remoteViewModel=new RemoteViewModel();
    if (navParams.data.item.remoteViewModel != null) {
      this.item.remoteViewModel.exec_file_seq=navParams.data.item.remoteViewModel.exec_file_seq;
      this.item.remoteViewModel.remote_conn_id =
        navParams.data.item.remoteViewModel.remote_conn_id;

        this.item.remoteViewModel.remote_conn_string =
        navParams.data.item.remoteViewModel.remote_conn_string;

    }

    if (this.change_mode === "n") {
      this.get_default_path();
      this.get_conn_string("SSIS-SSISDB");
    }
    if (this.change_mode === "c") {
      this.get_max_exec_file_seq();
      this.get_max_exec_seq();
    }
    this.CanEditBatch = navParams.data.CanEditBatch;
    this.CanFileBrowser = navParams.data.CanFileBrowser;

    this.title = "ssis執行設定";
  }
  SelectConn() {
    let modal = this.modalCtrl.create("ConnSelectModalPage", {
      select_remote_conn_id: this.item.remoteViewModel.remote_conn_id,
      conn_type_key:"065|006",
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.remoteViewModel.remote_conn_id = select_data.remote_conn_id;
      this.item.remoteViewModel.remote_conn_string = select_data.conn_string;
    });

    modal.present();
  }
  SelectPackageSrc() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.package_src_key,
      code_type: "067"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.package_src_key = select_data.code_key;
      this.item.package_src = select_data.code_desc;

    });

    modal.present();
  }

  openNavPage(parent_path) {
    let page;
    if(parent_path.indexOf("\\")>=0)
      page="DirectoryBrowserPage";
    if(parent_path.indexOf("/")>=0)
      page="FtpDirectoryBrowserPage";
    this.global.createLoader("連線中...");
    this.global.loading.present().then(() => {

      this.navCtrl.push(page, { parent_path: parent_path,parent_page:this,is_modal:true});
    });
    this.global.dismissLoading();
  }

  SelectParameterGroup() {
    let modal = this.modalCtrl.create("ParameterGroupSelectModelPage", {
      select_parameter_column_group: this.item.parameter_column_group
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.parameter_column_group = select_data.parameter_column_group;
    });

    modal.present();
  }
  openNavParameterColumnPage(item) {
    this.global.createLoader("連線中...");
    this.global.loading.present().then(() => {
      this.navCtrl.push("ParameterColumnPage", { item: item });
    });
    this.global.dismissLoading();
  }
  SelectPackageFilePath() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.ssis_package_path_key,
      code_type: "005"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.ssis_package_path_key = select_data.code_key;
      this.item.ssis_package_path = select_data.code_desc;
    });

    modal.present();
  }
  SelectProjectName() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.ssis_project_name_key,
      code_type: "005"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.ssis_project_name_key = select_data.code_key;
      this.item.ssis_project_name = select_data.code_desc;
    });

    modal.present();
  }


  get_max_exec_file_seq() {
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.FileSsisServices.GetMaxExecFileSeqAsync(
        this.func_no,
        this.item.exec_group
      ).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.exec_file_seq = data.Model;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }
  get_default_path() {
    this.global.createLoader("取得預設資料夾中...");
    this.global.loading.present().then(() => {
      this.FilePathServices.GetDefaultListsByAsync(this.func_key).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.default_path = data.Model;
            this.ssis_package_path = this.default_path.filter(
              (item, FilePathViewModel) => item.file_path_type_key === "004|SSIS"
            )[0];
            this.item.ssis_package_path_key = this.ssis_package_path.file_path_key;
            this.item.ssis_package_path = this.ssis_package_path.file_path;

            this.ssis_project_path = this.default_path.filter(
              (item, FilePathViewModel) => item.file_path_type_key === "004|SSISPJ"
            )[0];
            this.item.ssis_project_name_key = this.ssis_project_path.file_path_key;
            this.item.ssis_project_name = this.ssis_project_path.file_path;


            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }
  get_max_exec_seq() {
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.FileSsisServices.GetMaxExecSeqAsync(
        this.func_no,
        this.item.exec_group
      ).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.exec_seq = data.Model;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }
  get_conn_string(remote_conn_id: string) {
    this.global.createLoader("取得連線字串中...");
    this.global.loading.present().then(() => {
      this.ConnServices.GetConnAsync(remote_conn_id).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.remoteViewModel.remote_conn_string =
              data.Model.conn_string;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }

  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad FileSsisModalPage");
  }
}



