import { RemoteViewModel } from "../ViewModel/RemoteViewModel";
export class FileSsisViewModel {
  /// <summary>
  ///  轉檔序號
  /// <summary>
  public exec_file_seq: number;
  /// <summary>
  ///  轉檔群組
  /// <summary>
  public exec_group: string;
  /// <summary>
  ///  檔案搜尋關鍵字
  /// <summary>
  public file_keyword: string;
  /// <summary>
  ///  檔案最小長度
  /// <summary>
  public file_min_length: number;
  /// <summary>
  ///  檔案最大長度
  /// <summary>
  public file_max_length: number;

  /// <summary>
  ///  啟用註記
  /// <summary>
  public is_active: boolean;
  /// <summary>
  ///  封裝來源主鍵(參考共用代碼067)
  /// <summary>
  public package_src_key: string;

  /// <summary>
  ///  執行順序
  /// <summary>
  public exec_seq: number;
  /// <summary>
  ///  重複執行註記
  /// <summary>
  public can_rerun: boolean;
  /// <summary>
  ///  參數欄位群組
  /// <summary>
  public parameter_column_group: string;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;
  /// <summary>
  ///  備註
  /// <summary>
  public note: string;

  /// <summary>
  ///  封裝來源
  /// <summary>
  public package_src: string;

  /// <summary>
  ///  執行失敗停止
  /// </summary>
  public is_fail_stop: boolean;

  /// <summary>
  ///  ssis封裝路徑主鍵
  /// </summary>

  public ssis_package_path_key: string;

  /// <summary>
  ///  ssis專案名稱主鍵
  /// </summary>
  public ssis_project_name_key: string;



  /// <summary>
  ///  ssis封裝路徑
  /// </summary>

  public ssis_package_path: string;

  /// <summary>
  ///  ssis專案名稱
  /// </summary>
  public ssis_project_name: string;
  /// <summary>
  ///  啟用註記說明
  /// <summary>
  public is_active_desc: string;
  /// <summary>
  ///  啟用註記顏色
  /// <summary>
  public is_active_color: string;
  /// <summary>
  ///  啟用顏色
  /// <summary>
  public is_active_color_right: string;

  public remoteViewModel:RemoteViewModel;
}



