import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileSsisListResponse } from "../../Model/Response/FileSsisListResponse";
import { FileSsisResponse } from "../../Model/Response/FileSsisResponse";
import { FileSsisViewModel } from '../../Model/ViewModel/FileSsisViewModel';
import { FileServicesProvider } from '../file-services/file-services';

/*
  Generated class for the FileSsisServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileSsisServicesProvider extends FileServicesProvider {
  private FileSsiss: FileSsisListResponse;
  private FileSsis: FileSsisResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "FileSsis";
    console.log("Hello FileSsisServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
    if(exec_file_seq===0)
    {
      sub_url = "FileSsis?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&order_type={5}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);
    }
    else
    {
      sub_url = "FileSsis/Select?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&exec_file_seq={5}&order_type={6}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,exec_file_seq,order_type);
    }
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileSsiss = new FileSsisListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileSsiss;
    });
  }



  public PostPutAsync(FileSsisViewModel:FileSsisViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FileSsis";
    else
    sub_url = "FileSsis/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileSsisViewModel.exec_file_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileSsisViewModel,mode).map(
      item => {
        this.FileSsis = new FileSsisResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileSsis;
      }
    );
  }
}



