import { SingleModelResponse } from "./SingleModelResponse";
import { FileSsisViewModel } from "../ViewModel/FileSsisViewModel";
import { RemoteViewModel } from "../ViewModel/RemoteViewModel";
export class FileSsisResponse extends SingleModelResponse<FileSsisViewModel> {
  Model: FileSsisViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new FileSsisViewModel();
      this.Model.exec_file_seq = obj.Model.exec_file_seq;
      this.Model.exec_group = obj.Model.exec_group;
      this.Model.file_keyword = obj.Model.file_keyword;
      this.Model.file_min_length = obj.Model.file_min_length;
      this.Model.file_max_length = obj.Model.file_max_length;
      this.Model.is_active = obj.Model.is_active;
      this.Model.package_src_key = obj.Model.package_src_key;
      this.Model.exec_seq = obj.Model.exec_seq;
      this.Model.can_rerun = obj.Model.can_rerun;
      this.Model.parameter_column_group = obj.Model.parameter_column_group;
      this.Model.creator = obj.Model.creator;
      this.Model.create_time = obj.Model.create_time;
      this.Model.modifier = obj.Model.modifier;
      this.Model.last_update_time = obj.Model.last_update_time;
      this.Model.note = (obj && obj.Model.note) || null;
      this.Model.package_src = obj.Model.package_src;
      this.Model.is_active_desc = obj.Model.is_active_desc;
      switch (obj.Model.is_active) {
        case false: {
          this.Model.is_active_color = "danger";
          break;
        }

        default:
          this.Model.is_active_color = "active";
      }
      this.Model.ssis_package_path_key = obj.Model.ssis_package_path_key;
      this.Model.ssis_package_path = obj.Model.ssis_package_path;

      this.Model.is_fail_stop = obj.Model.is_fail_stop;
      this.Model.ssis_project_name_key = obj.Model.ssis_project_name_key;
      this.Model.ssis_project_name = obj.Model.ssis_project_name;
      if (obj.Model.remoteViewModel != null) {
        var t: RemoteViewModel = new RemoteViewModel();
        t.exec_file_seq = obj.Model.remoteViewModel.exec_file_seq;
        t.remote_conn_id = obj.Model.remoteViewModel.remote_conn_id;
        t.creator = obj.Model.remoteViewModel.creator;
        t.create_time = obj.Model.remoteViewModel.create_time;
        t.modifier = obj.Model.remoteViewModel.modifier;
        t.last_update_time = obj.Model.remoteViewModel.last_update_time;
        t.remote_conn_string = obj.Model.remoteViewModel.remote_conn_string;
        this.Model.remoteViewModel = t;
      }
    }
  }
}



